.center {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
  position: relative;
  height: 100vh;
}
.center__container {
  width: 100%;
  display: flex;
  flex-direction: column;
  row-gap: 30px;
  padding: 20px 16.35% 50px;
  overflow: scroll;
  box-sizing: border-box;
}
.center__container::-webkit-scrollbar {
  display: none;
}
.center__title {
  padding: 0 0 10px 20px;
}